import React, { Fragment, useState } from 'react';
import "../../../Css/Kyc-form.css";
import data from '../../JsonFile/Stateanddistrict.json';



function Kycform() {

    const [show, setShow] = useState("d-block")
    const india = data.states;
    const [stateDistrict, setStateDistrict] = useState([" "]);

    const handleSelectChange = (event) => {
        const stateName = event.target.value;
        console.log(stateName)
        setStateDistrict(india.find(e => e.state === stateName).districts)
    }
    return (
        <Fragment>
            <h1 className='text-center text-white fs-2' style={{ paddingTop: "10rem", height: "15rem" }}>KYC FROM</h1>
            <br />
            <div className="container-fluid p-5 kyccontainer-fluid">
                <div className="container kyccontainer">
                    <form className='p-3' method='get'>
                        <div className="my-3">
                            <label className='kyclabel' htmlFor="name">Name of Applicant / Owner*</label>
                            <input type="text" className="form-control kyctext" id="name" name="name" />
                        </div>
                        <div className="mb-3">
                            <label className='kyclabel' htmlFor="dob">Applicant / Owner Date of Birth*</label>
                            <input type="date" className="form-control kyctext" id="dob" name="dob" />
                        </div>
                        <div className="mb-3">
                            <label className='kyclabel' htmlFor="adharnumber">Aadhar Card</label>
                            <input type="number" className="form-control" id="adharnumber" name="adharnumber" />
                        </div>
                        <div className="mb-3">
                            <label className='kyclabel' htmlFor="pancardnumber">Pan Card</label>
                            <input className="form-control" type="text" pattern='[A-Z]*' id="pancardnumber" name="pancardnumber" />
                        </div>
                        <div className="mb-3">
                            <label className='kyclabel' htmlFor="mobile">Adhar Link Mobile No.*</label>
                            <input type="mobile" className="form-control kyctext" id="mobile" name="mobile" />
                        </div>
                        <div className="mb-3">
                            <label className='kyclabel' htmlFor="email">Email Id*</label>
                            <input type="email" id="email" pattern="[A-Z]*" title="Please enter only uppercase letters" className="form-control kyctext" name="email" />
                        </div>
                        <div className="mb-3">
                            <label className='kyclabel'>Gender</label>
                            <div className="form-check">
                                <input className="ms-1 form-check-input" type="radio" name="gender" id="gender-male" value="male" />
                                <label className="ms-1 kyclabel form-check-label" htmlFor="gender-male">Male</label>
                            </div>
                            <div className="form-check">
                                <input className="ms-1 form-check-input" type="radio" name="gender" id="gender-female" value="female" />
                                <label className="ms-1 kyclabel form-check-label" htmlFor="gender-female">Female</label>
                            </div>
                            <div className="form-check">
                                <input className="ms-1 form-check-input" type="radio" name="gender" id="gender-other" value="other" />
                                <label className="ms-1 kyclabel form-check-label" htmlFor="gender-other">Other</label>
                            </div>
                        </div>
                        <div className="mb-3">
                            <label className='kyclabel' htmlFor="father-name">Name of Father</label>
                            <input type="text" className="form-control kyctext" id="father-name" name="father-name" />
                        </div>
                        <div className="mb-3">
                            <label className='kyclabel' htmlFor="mother-name">Name of Mother</label>
                            <input type="text" className="form-control kyctext" id="mother-name" name="mother-name" />
                        </div>

                        <div className="mb-3">
                            <label className='kyclabel' htmlFor="marital-status">Marital Status</label>
                            <select className="form-control marital" id="marital-status" name="marital-status" onChange={(e) => {
                                if (e.target.value === "married") {
                                    setShow("d-block");
                                }
                                else {
                                    setShow("d-none");
                                }
                            }}>
                                <option value="married">Married</option>
                                <option value="unmarried">Unmarried</option>
                            </select>
                        </div>
                        <div className={`mb-3 ${show} `} id='kycwife'>
                            <label className='kyclabel' htmlFor="spouse-name">Wife Name</label>
                            <input type="text" className="form-control kyctext" id="spouse-name" name="spouse-name" />
                        </div>

                        <div className="mb-3">
                            <label htmlFor="owneraddress" className='kyclabel' >Owner Address*</label>
                            <textarea name="owneraddress" id="owneraddress" />
                        </div>
                        <div className="mb-3">
                            <h1 className='h5'>Proposed location for opening "Swabhimaan Kendra"</h1>
                            <label htmlFor="addresline" className='kyclabel' >Address Line 1*</label>
                            <textarea name="addresline" id="addresline" />
                            <label htmlFor="addresline2" className='kyclabel' >Address Line 2</label>
                            <textarea name="addresline2" id="addresline2" />
                        </div>
                        <div className={`mb-3 `} id='kendraadress'>
                            <label className='kyclabel' htmlFor="kendrarea">Category of Area *</label>
                            <select className="form-control kyctext" id="kendrarea" name="kendrarea" >
                                <option hidden>Select</option>
                                <option value="">Rural</option>
                                <option value="">Urban</option>
                            </select>
                        </div>
                        <div className="mb-3">
                            <label className='kyclabel ' htmlFor="state">State</label>
                            <select className="form-select marital" aria-label="Default select example" id="state" name="state" onChange={handleSelectChange}>
                                <option hidden>Select State </option>
                                {india.map((item, index) => (
                                    <option key={index} value={item.state}>
                                        {++index} {item.state}
                                    </option>
                                ))
                                }
                            </select>
                        </div>
                        <div className="mb-3">
                            <label className='kyclabel ' htmlFor="district">District</label>
                            <select className="form-select marital" id="district" name="district" aria-label="Default select example">
                                <option hidden>Select district </option>
                                {stateDistrict.map((item, index) => {
                                    return (
                                        <option key={index} value={item}>{item}</option>
                                    )
                                })}
                            </select>
                        </div>
                        <div className="mb-3">
                            <label className='kyclabel' htmlFor="pin-code">Pin Code*</label>
                            <input type="number" className="form-control kyctext" pattern="[0-9]{8}" title="pin code is not more than 8 digits" id="pin-code" name="pin-code" />
                        </div>
                        <div className="mb-3">
                            <label className='kyclabel' htmlFor="blood-group">Blood Group</label>
                            <input type="text" className="form-control kyctext" id="blood-group" name="blood-group" />
                        </div>
                        <div className="mb-3">
                            <label className='kyclabel' htmlFor="qualification">Qualification</label>
                            <input type="text" className="form-control kyctext" id="qualification" name="qualification" />
                        </div>
                        <div className="mb-3">
                            <label className='kyclabel' htmlFor="medical-history">Medical History</label>
                            <input type="text" className="form-control kyctext" id="medical-history" name="medical-history" />
                        </div>

                        <div className="mb-3">
                            <label className='kyclabel form-label' htmlFor="formFileMd">Id Proof</label>
                            <input className="form-control form-control-md" id="formFileMd" type="file" />
                        </div>

                        <div className="mb-3">
                            <label className='kyclabel' htmlFor="domecile">Domecile Certificate</label>
                            <input className="form-control form-control-md" id="domecile" name="domecile" type="file" />
                        </div>
                        <div className="mb-3">
                            <label className='kyclabel' htmlFor="adharcard">Aadhar Card</label>
                            <input type="file" className="form-control form-control-md" id="adharcard" name="adharcard" />
                        </div>
                        <div className="mb-3">
                            <label className='kyclabel' htmlFor="pancard">Pan Card</label>
                            <input className="form-control form-control-md" type="file" id="pancard" name="pancard" />
                        </div>
                        <div className="mb-3">
                            <label className='kyclabel' htmlFor="lpc">LPC Type File</label>
                            <input type="file" className="form-control form-control-md" id="lpc" name="lpc" />
                        </div>
                        <div className="mb-3">
                            <h1 className='h3 text-danger'>Application Submission Fee: <span className='text-success fs-4'>â‚¹23700 Only</span></h1>
                        </div>
                        <button type="submit" className="btn w-100 text-light" style={{ backgroundColor: "#336699" }}>Submit</button>
                    </form>

                </div>
            </div>
        </Fragment>
    )
}

export default Kycform
